@import url(https://fonts.googleapis.com/css2?family=Sigmar+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gochi+Hand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: url('https://mocah.org/uploads/posts/547224-landscape-wallpaper.gif');
  background-size: cover;
}

.login-title {
  -webkit-background-clip: text;
          background-clip: text;
  background-image: url('https://i.pinimg.com/originals/62/39/4d/62394d753859943e6a1a36443ef78795.gif');
  background-size: contain;
  color: transparent;
  font-family: Sigmar One , cursive;
  font-size: 60px;
  line-height: 0.75;
  margin: 10px;
  text-align: center;
  -webkit-text-stroke: 2px white;
}

.login-panel {
  align-items: center;
  background-color: #d2d7df;
  border: 1px solid black;
  border-radius: 16px;
  box-shadow: 1px 3px 10px black;
  display: flex;
  flex-direction: column;
  margin: 15% auto;
  padding: 20px;
  width: 300px;
}

.login-panel img {
  margin: 10px;
  padding: 5px;
}

.login-panel input {
  margin: 10px;
  padding: 5px;
}

.login-panel .btn-login-screen {
  background-color: darkslategrey;
  border: none;
  border-radius: 5px;
  box-shadow: 3px 3px 3px black;
  color: white;
  padding: 10px 24px;
  width: 100%;
}

.login-panel .btn-login-screen:hover {
  background-color: aquamarine;
  color: black;
  cursor: pointer;
  font-size: 16px;
}

.header {
  align-items: center;
  background-color: #3a3b3c;
  box-shadow: 3px 3px 6px black;
  display: flex;
  justify-content: space-between;
  opacity: 0.75;
  overflow: hidden;
  padding: 15px;
  position: fixed;
  top: 0;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
  width: 100%;
}

.header-logos {
  align-items: center;
}

.header-infos {
  align-items: center;
  display: flex;
  padding: 1px 50px;
}

.header:hover {
  opacity: 1;
}

.header-wings {
  width: 70px;
}

.header-game-title {
  -webkit-background-clip: text;
          background-clip: text;
  background-image: url('https://i.pinimg.com/originals/62/39/4d/62394d753859943e6a1a36443ef78795.gif');
  background-size: contain;
  color: transparent;
  font-family: Sigmar One , cursive;
  font-size: 60px;
  line-height: 0.75;
  text-align: center;
  -webkit-text-stroke: 2px white;
}

.header-img {
  border-radius: 50%;
  box-shadow: 1px 1px 8px black;
  margin: 0 20px;
  padding: 8px;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.header-text {
  color: #e4e6eb;
  font-family: Alfa Slab One , cursive;
  font-size: 34px;
  margin: 0 20px;
  text-shadow: 3px 3px 3px black;
  -webkit-text-stroke: 1px black;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.header-img:hover, .header-text:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.loading {
  color: whitesmoke;
  font-family: Gochi Hand , cursive;
  font-size: 80px;
  font-weight: 700;
  margin: 250px auto;
  text-align: center;
  text-shadow: 3px 3px 3px black;
  -webkit-text-stroke: 1px black;
}

.game-content {
  align-items: center;
  background-color: #444;
  border-radius: 10px;
  box-shadow: 0 0 10px 2px black;
  display: flex;
  flex-direction: column;
  height: auto;
  margin: 50px auto;
  margin-top: 150px;
  max-height: 800px;
  max-width: 800px;
  padding: 50px 80px;
  top: 100px;
  width: auto;
}

.bat {
  margin: 40px 0;
  width: 180px;
}

.question {
  color: whitesmoke;
  font-family: Bebas Neue , cursive;
  letter-spacing: 2px;
}

.game-content .buttons {
  box-shadow: none;
  display: inline-block;
  height: auto;
  margin: 1px;
  padding: 1px;
  text-align: center;
  width: auto;
}

.game-content button {
  background-color: #2069e0;
  border-radius: 5px;
  box-shadow: 3px 3px 3px black;
  font-size: 18px;
  font-weight: 700;
  height: auto;
  margin-left: 10px;
  margin-top: 10px;
  padding: 10px 40px;
  white-space: nowrap;
  width: auto;
}

.game-content button:hover {
  background-color: #fad47c;
  cursor: pointer;
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}

.game-content button:disabled {
  background-color: whitesmoke;
  -webkit-transform: unset;
          transform: unset;
}

.correct {
  border: 3px solid rgb(6, 240, 15);
}

.incorrect {
  border: 3px solid red;
}

button .next-question {
  align-items: center;
  border-radius: 50%;
  color: white;
  display: inline-flex;
  flex-direction: column;
  font-size: 18px;
  font-weight: 700;
  height: 120px;
  justify-content: center;
  margin: 20px;
  padding: 10px;
  text-align: center;
  white-space: normal;
  width: 120px;
}

button .feedback {
  align-items: center;
  border-radius: 50%;
  color: white;
  display: inline-flex;
  flex-direction: column;
  font-size: 18px;
  font-weight: 700;
  height: 120px;
  justify-content: center;
  margin: 20px;
  padding: 10px;
  text-align: center;
  white-space: normal;
  width: 120px;
}

.timer {
  background-color: whitesmoke;
  border-radius: 50%;
  box-shadow: 0 0  10px 4px black;
  font-family: Bebas Neue , cursive;
  font-size: 100px;
  height: 140px;
  line-height: 150px;
  margin: 0 auto;
  text-align: center;
  width: 140px;
}

.feedback-content {
  align-items: center;
  background-color: #444;
  border-radius: 10px;
  box-shadow: 4px 4px 50px black;
  display: flex;
  flex-direction: column;
  height: auto;
  margin: 1px auto;
  margin-top: 180px;
  max-height: 700px;
  max-width: 500px;
  padding: 20px 40px;
  width: auto;
}

.feedback-text, .feedback-score {
  color: aliceblue;
  font-family: 'Press Start 2p' , cursive;
  font-size: 40px;
  text-shadow: 3px 3px black;
}

.feedback-text:hover, .feedback-score:hover {
  -webkit-animation: movein 0.3s infinite;
          animation: movein 0.3s infinite;
}

@-webkit-keyframes movein {

  0% { color: #444; }

  100% { color: whitesmoke; }
}

@keyframes movein {

  0% { color: #444; }

  100% { color: whitesmoke; }
}

.fireworks {
  padding: 20px 1px;
}

.feedback-content button {
  background-color: #2069e0;
  border-radius: 5px;
  box-shadow: 3px 3px 3px black;
  font-size: 18px;
  font-weight: 700;
  height: auto;
  margin-left: 10px;
  margin-top: 10px;
  padding: 10px 40px;
  white-space: nowrap;
  width: auto;
}

.feedback-content button:hover {
  background-color: #fad47c;
  cursor: pointer;
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}

.ranking-content {
  background-color: #444;
  border-radius: 10px;
  box-shadow: 3px 3px 3px black;
  height: auto;
  margin: 80px auto;
  max-height: 600px;
  max-width: 800px;
  padding: 20px;
  width: auto;
}

.ranking-content-inside {
  height: auto;
  max-height: 600px;
  max-width: 800px;
  overflow: auto;
  width: auto;
}

.ranking-content-inside h1 {
  -webkit-animation: movein 0.3s;
          animation: movein 0.3s;
  -webkit-animation-iteration-count: 3;
          animation-iteration-count: 3;
  color: whitesmoke;
  font-family: 'Press Start 2p' , cursive;
  font-size: 40px;
  text-align: center;
  text-shadow: 3px 3px 3px black;
}

@-webkit-keyframes movein {

  0% { color: green; }

  100% { color: yellow; }

}

@keyframes movein {

  0% { color: green; }

  100% { color: yellow; }

}

.ranking-content img {
  border-radius: 50%;
  box-shadow: 0 0  8px 3px black;
  width: 80px;
}

.ranking-content-items {
  background-color: rgb(206, 206, 206);
  border: 1px solid black;
  border-radius: 20px;
  box-shadow: 3px 3px 3px black;
  display: flex;
  justify-content: space-around;
  margin-left: 40px;
  margin-right: 50px;
  margin-top: 20px;
  padding: 10px;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  width: 600px;
}

.ranking-content-items:hover {
  background-color: white;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.ranking-content-items span {
  font-family: 'Press Start 2p' , cursive;
  font-size: 22px;
  font-weight: 700;
  line-height: 80px;
}

.div-button {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.div-button button {
  background-color: whitesmoke;
  border-radius: 5px;
  box-shadow: 3px 3px 3px black;
  font-size: 18px;
  font-weight: 700;
  height: auto;
  margin-left: 10px;
  margin-top: 10px;
  padding: 10px 40px;
  white-space: nowrap;
  width: auto;
}

.div-button button:hover {
  background-color: #fad47c;
  cursor: pointer;
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
}

